@use "../../styles/partials/breakpoints" as *;
@use "../../styles/partials/colors";
@use "../../styles/partials/fonts";
@use "../../styles/partials/mixins";
@use "../../styles/partials/units";

.video-page {
    padding-top: 0.25rem;

    @include tablet {
        padding-top: 0;
    }

    &__status-message {
        @include fonts.sectionheader;
        text-align: center;
        margin: 20vh auto 0;
    }
}

.bottom-section {
    padding: 0 1rem 1rem;

    @include tablet {
        padding: 0 2.25rem 1.25rem 2.25rem;
    }

    @include desktop {
        padding-bottom: 0;
    }

    &__content {
        margin: auto;
        max-width: units.$max-width;
        @include mixins.flexbox($wrap: wrap);

        @include desktop {
            flex-wrap: nowrap;
        }
    }

    &__video-details-container {
        padding-top: 1rem;
        width: 100%;

        @include tablet {
            padding-top: 1.5rem;
        }

        @include desktop {
            padding-top: 1.875rem;
            padding-bottom: 4rem;
            @include mixins.divider(right);
            max-width: 68.1%;
        }
    }

    &__next-videos-container {
        padding-top: 1rem;
        @include mixins.divider(top);
        width: 100%;

        @include tablet {
            padding-top: 1.5rem;
        }

        @include desktop {
            border: none;
            padding-top: 1.875rem;
            padding-bottom: 4rem;
            padding-left: 1.1875rem;
            width: auto;
        }
    }
}

