@use "../../styles/partials/breakpoints" as *;
@use "../../styles/partials/units";
@use "../../styles/partials/mixins";

.navbar {
    padding: 0.25rem 0.5rem 0.25rem;

    @include tablet {
        padding: 0.5rem 1.75rem;
    }

    &__content {
        margin: auto;
        max-width: units.$max-width;
        @include mixins.flexbox($align-items: center, $wrap: wrap);
    }

    &__logo-container {
        margin: 0.5rem;
        flex: 100%;

        @include tablet {
            margin-right: auto;
            flex: 0;
        }

        @include desktop {
            margin-left: 0;
        }
    }

    &__search-container {
        margin: 0.5rem;
        flex: 1;

        @include tablet {
            margin-right: 1rem;
            flex: 0 15rem;
        }
    }

    &__avatar-container {
        margin: 0.5rem;

        @include tablet {
            flex: 0;
            order: 2;
        }

        @include desktop {
            margin-right: 0.75rem;
        }
    }

    &__upload-container {
        margin: 0.5rem;
        flex: 100%;

        @include tablet {
            margin-right: 0.75rem;
            flex: 0 9.75rem;
        }

        @include desktop {
            margin-right: 1.75rem;
            flex: 0 9.25rem;
        }
    }
}
